      </svg>
      <span id="header-title">Chromosome</span>
    </el-row>
    <el-row type="flex" justify="center">
      <el-upload
        id="upload-gene-file"
        class="upload-gene-file"
        drag
        action
        :on-change="uploadGeneData"
        :auto-upload="false"
        :multiple="false"
        accept=".txt"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          Drop file here or
          <em>click to upload</em>
        </div>
        <div class="el-upload__tip" slot="tip">txt file with no more than 50 sequences</div>
      </el-upload>
    </el-row>
    <el-row type="flex" justify="center">
      <el-button
        type="default"
        class="submit-button"
        :disabled="disabled"
        @click="handleSubmitGeneData"
        icon="el-icon-magic-stick"
        round
      >Process</el-button>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-row>
          <p class="gene-label">Content Preview:</p>
        </el-row>
        <el-row>
          <el-input
            id="preview-text"
            class="preview-text"
            type="textarea"
            v-model="previewText"
            :autosize="{ minRows: 15, maxRows: 15}"
            :disabled="true"
          ></el-input>
        </el-row>
      </el-col>
      <el-col :span="12">
        <el-row>
          <p class="gene-label">Result:</p>
        </el-row>
        <el-row>
          <el-input
            id="result-text"
            class="result-text"
            type="textarea"
            v-model="outputText"
            :autosize="{ minRows: 15, maxRows: 15}"
            :disabled="true"
          ></el-input>
        </el-row>
      </el-col>
