</template>
<script>
export default {
  data() {
    return {
      disabled: this.$store.state.disabled,
      previewText: this.$store.state.previewText,
      outputText: this.$store.state.outputText,
      uploadForm: new FormData()
    };
  },
  methods: {
    uploadGeneData(file, fileList) {
      if (fileList.length > 1) {
        fileList.splice(0, 1);
      }
      this.disabled = false;
      this.outputText = "";

      let uploadFile = file.raw;
      let reader = new FileReader();
      let _this = this;

      reader.onload = function(e) {
        _this.previewText = e.target.result;
      };

      reader.readAsText(uploadFile);

      this.uploadForm.set("file", file.raw);
    },

    handleSubmitGeneData(event) {
      let url = "/api/upload";
      let config = { headers: { "Content-Type": "multipart/form-data" } };

      let formData = this.uploadForm;

      this.axios.post(url, formData, config).then(response => {
        console.log(response.data);
        this.outputText = response.data;
        this.disabled = true;
      });
    }
  }
};
</script>


<style>
.gene-label {
  color: #606266;
  font-size: 14px;
}
#header-title {
  font-size: 28px;
  font-weight: bold;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 4px;
}
#preview-text,
#result-text {
  color: black;
}
</style>



